;color:#84a0c6}.hyperlink{font-weight:bold;transition:.4s linear;transform:translateY(0rem)}.hyperlink:hover{transform:translateY(-0.5rem)}#introduction{flex-direction:unset;padding:8.2rem 1.5rem}#introduction .introduction-container{display:flex;position:relative;flex-direction:column;justify-content:center;align-items:center}#introduction .introduction-container .avatar{margin:auto;width:16rem !important}#introduction .introduction-container .avatar .image-title{border-radius:100%;height:16rem !important;max-width:none !important;animation:3s ease-in-out infinite;animation-name:floating}#introduction .introduction-container .content{margin-left:auto;margin-top:2.5rem;margin-bottom:2rem}#introduction .introduction-container .content .title::after{content:"|";font-size:3rem;margin-top:4px;animation:blink 2.5s infinite}#projects{background-color:#101415;padding:6rem 1.5rem !important}#projects .projects-container{display:flex;flex-direction:column;justify-content:center;align-items:center}#projects .projects-container h1{color:inherit !important;font-size:36px;font-weight:bold;align-items:center;margin-bottom:1rem}#about .about-container .about-description{color:#c5c8c9;font-size:18px;font-weight:normal}#about .about-container .columns{padding:12px 0px 12px 0px}@media only screen and (max-width: 600px){#about .about-container .columns .column{text-align:center}}#about .about-container .columns .column .button{font-weight:bold}@media only screen and (max-width: 600px){#about .about-container .columns .column .button{border-radius:4px;background-color:#192022 !important}}@media only screen and (min-width: 600px){#about .about-container .columns .column .button{border:none !important;margin:3px !important}}#about .about-container .columns .column .button:hover,#about .about-container .columns .column .button:focus{color:#c5c8c9;box-shadow:none !important;border-color:none !important;transform:translateX(0.5rem)}#about .about-container h1{color:inherit !important;font-si